
import { defineComponent, reactive, ref, toRefs } from "vue";
import useRules from "@/hooks/useRules";
import { useRoute, useRouter } from "vue-router";
import { useStore } from "vuex";
import { ATS } from "@/store/actionTypes";
import $message from "@/components/Message";
import { register } from "@/api";

export default defineComponent({
  name: "Login",
  setup() {
    const route = useRoute();
    const router = useRouter();
    const store = useStore();
    const state = reactive({
      isLogin: route.query.state === "new-user" ? false : true,
      loginInfo: {
        name: "",
        password: "",
      },
      registerInfo: {
        name: "",
        password: "",
      },
      loginRef: ref<null | HTMLFormElement>(null),
      registerRef: ref<null | HTMLFormElement>(null),
    });

    const { nameRules, passwordRules } = useRules();

    // 登录
    const login = async () => {
      const result = state.loginRef?.validate();
      if (!result) {
        $message({
          type: "warn",
          message: "请输入正确的数据",
        });
        return;
      }

      try {
        await store.dispatch(ATS.LOGIN_AND_FETCH_CURRENT_USER, state.loginInfo);
        $message({
          type: "success",
          message: "登录成功,即将跳转至首页",
        });
        setTimeout(() => {
          router.replace("/");
        }, 500);
      } catch (error) {
        //
      }
    };

    // 注册
    const registerUser = async () => {
      const result = state.registerRef?.validate();
      if (!result) {
        $message({
          type: "warn",
          message: "请输入正确的数据",
        });
        return;
      }

      try {
        await register(state.registerInfo);
        $message({
          type: "success",
          message: "注册成功",
        });
      } catch (error) {
        //
      }
    };

    return {
      ...toRefs(state),
      nameRules,
      passwordRules,
      login,
      registerUser,
    };
  },
});
