        <h1 class="text-white fw-bolder mt-3">SandClock</h1>
      </div>
      <div class="col-lg-5 col-12 login-part">
        <div
          class="login-part d-flex justify-content-center align-items-center"
        >
          <div class="login-wrapper w-50">
            <ul class="nav nav-tabs border-bottom-0 justify-content-between">
              <li class="nav-item">
                <button
                  :class="['nav-link', { active: isLogin }]"
                  id="login-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#login"
                  type="button"
                >
                  登录
                </button>
              </li>
              <li class="nav-item">
                <button
                  :class="['nav-link', { active: !isLogin }]"
                  id="register-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#register"
                  type="button"
                >
                  注册
                </button>
              </li>
            </ul>
            <div class="tab-content">
              <!-- 登录 -->
              <div
                :class="['tab-pane fade', { 'show active': isLogin }]"
                id="login"
              >
                <h2 class="text-center my-4">
                  欢迎来到 <br />
                  SandClock 社区
                </h2>
                <v-form ref="loginRef">
                  <v-form-item label="用户名">
                    <v-input
                      placeholder="请输入用户名"
                      v-model="loginInfo.name"
                      :rules="nameRules"
                    />
                  </v-form-item>

                  <v-form-item label="密码" class="my-3">
                    <v-input
                      type="password"
                      placeholder="请输入密码"
                      v-model="loginInfo.password"
                      :rules="passwordRules"
                    />
                  </v-form-item>

                  <v-btn color="primary" block class="py-2 px-4" @click="login">
                    登录
                  </v-btn>
                </v-form>
              </div>
              <!-- 注册 -->
              <div
                :class="['tab-pane fade', { 'show active': !isLogin }]"
                id="register"
              >
                <h2 class="text-center my-4">
                  欢迎注册 <br />
                  SandClock 账号
                </h2>
                <v-form ref="registerRef">
                  <v-form-item label="用户名">
                    <v-input
                      placeholder="请输入用户名"
                      v-model="registerInfo.name"
                      :rules="nameRules"
                    />
                  </v-form-item>

                  <v-form-item label="密码" class="my-3">
                    <v-input
                      type="password"
                      placeholder="请输入密码"
                      v-model="registerInfo.password"
                      :rules="passwordRules"
                    />
                  </v-form-item>

                  <v-btn
                    color="primary"
                    block
                    class="py-2 px-4"
                    @click="registerUser"
                  >
                    注册
                  </v-btn>
                </v-form>
              </div>
            </div>
          </div>
        </div>
        <div class="position-absolute bottom-0 start-0 w-100 py-1">
          <footer class="text-primary text-center">
            &copy; {{ new Date().getFullYear() }} , writed by Momento
          </footer>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref, toRefs } from "vue";
import useRules from "@/hooks/useRules";
import { useRoute, useRouter } from "vue-router";
import { useStore } from "vuex";
import { ATS } from "@/store/actionTypes";
import $message from "@/components/Message";
import { register } from "@/api";

export default defineComponent({
  name: "Login",
  setup() {
    const route = useRoute();
    const router = useRouter();
    const store = useStore();
    const state = reactive({
      isLogin: route.query.state === "new-user" ? false : true,
      loginInfo: {
        name: "",
        password: "",
      },
      registerInfo: {
        name: "",
        password: "",
      },
      loginRef: ref<null | HTMLFormElement>(null),
      registerRef: ref<null | HTMLFormElement>(null),
    });

    const { nameRules, passwordRules } = useRules();

    // 登录
    const login = async () => {
      const result = state.loginRef?.validate();
      if (!result) {
        $message({
          type: "warn",
          message: "请输入正确的数据",
        });
        return;
      }

      try {
        await store.dispatch(ATS.LOGIN_AND_FETCH_CURRENT_USER, state.loginInfo);
        $message({
          type: "success",
          message: "登录成功,即将跳转至首页",
        });
        setTimeout(() => {
          router.replace("/");
        }, 500);
      } catch (error) {
        //
      }
    };

    // 注册
    const registerUser = async () => {
      const result = state.registerRef?.validate();
      if (!result) {
        $message({
          type: "warn",
          message: "请输入正确的数据",
        });
        return;
      }

      try {
        await register(state.registerInfo);
        $message({
          type: "success",
          message: "注册成功",
        });
      } catch (error) {
        //
      }
    };

    return {
      ...toRefs(state),
      nameRules,
      passwordRules,
      login,
      registerUser,
    };
  },
});
</script>

<style src="./index.scss" lang="scss" scoped></style>
